    const refreshInterval = ref(null)
    const lastUpdated = ref(null)
    const showTechnicalDetails = ref(false)
    const mintingNft = ref(false)
    const showImagePopup = ref(false)
    const nftCollapsed = ref(false) // По умолчанию развернута
    const mintCopied = ref(false)
    
    const toast = ref({
      show: false,
      message: '',
      type: 'info',
      duration: 3000
    })

    const tokenId = computed(() => route.params.tokenId)
    const wallet = computed(() => store.getters.walletAddress)
    
    const hasUserLiked = computed(() => {
      if (!tokenData.value || !userTag.value) return false;
      return tokenData.value.usersLiked && tokenData.value.usersLiked.includes(userTag.value);
    })
    
    const hasUserDisliked = computed(() => {
      if (!tokenData.value || !userTag.value) return false;
      return tokenData.value.usersDislikes && tokenData.value.usersDislikes.includes(userTag.value);
    })

    const loadTokenData = async (showLoading = true) => {
      if (showLoading) {
        loading.value = true
      }
      error.value = ''
      
      try {
        if (!tokenId.value) {
          throw new Error('Token ID is required')
        }
        
        // Используем новый API endpoint
        const response = await axios.get(`/tokens/${tokenId.value}`)
        
        if (response.data.success) {
          // Плавное обновление без мерцания
          if (tokenData.value) {
            Object.assign(tokenData.value, response.data.data)
          } else {
            tokenData.value = response.data.data
          }
          console.log('Token data loaded:', tokenData.value); // Debug log
          console.log('Token status:', tokenData.value?.status); // Debug log
          lastUpdated.value = new Date()
        } else {
          throw new Error(response.data.message || 'Failed to load token data')
        }
      } catch (error) {
        console.error('Error loading token data:', error)
        if (showLoading) {
          error.value = error.response?.data?.message || error.message || 'Error loading token data'
        }
      } finally {
        if (showLoading) {
          loading.value = false
        }
      }
    }

    const handleLike = async () => {
      if (!userTag.value) {
        showToast('Please login to like this token', 'error')
        return
      }
      
      try {
        // Если пользователь уже поставил дизлайк, удаляем его
        if (hasUserDisliked.value) {
          await removeDislike()
        }
        
        // Если пользователь уже поставил лайк, удаляем его
        if (hasUserLiked.value) {
          await removeLike()
          return
        }
        
        // Добавляем лайк
        const response = await axios.post(`/token/${tokenId.value}/like`, { userTag: userTag.value })
        
        if (response.data.success) {
          // Обновляем данные токена
          tokenData.value = response.data.tokenLaunch
          showToast('You liked this token!', 'success')
        } else {
          throw new Error(response.data.message || 'Failed to like token')
        }
      } catch (error) {
        console.error('Error liking token:', error)
        showToast(error.response?.data?.message || error.message || 'Error liking token', 'error')
      }
    }
    
    const handleDislike = async () => {
      if (!userTag.value) {
        showToast('Please login to dislike this token', 'error')
        return
      }
      
      try {
        // Если пользователь уже поставил лайк, удаляем его
        if (hasUserLiked.value) {
          await removeLike()
        }
        
        // Если пользователь уже поставил дизлайк, удаляем его
        if (hasUserDisliked.value) {
          await removeDislike()
          return
        }
        
        // Добавляем дизлайк
        const response = await axios.post(`/token/${tokenId.value}/dislike`, { userTag: userTag.value })
        
        if (response.data.success) {
          // Обновляем данные токена
          tokenData.value = response.data.tokenLaunch
          showToast('You disliked this token', 'info')
        } else {
          throw new Error(response.data.message || 'Failed to dislike token')
        }
      } catch (error) {
        console.error('Error disliking token:', error)
        showToast(error.response?.data?.message || error.message || 'Error disliking token', 'error')
      }
    }
    
    const removeLike = async () => {
      try {
        const response = await axios.post(`/token/${tokenId.value}/unlike`, { userTag: userTag.value })
        
        if (response.data.success) {
          // Обновляем данные токена
          tokenData.value = response.data.tokenLaunch
          showToast('Like removed', 'info')
        } else {
          throw new Error(response.data.message || 'Failed to remove like')
        }
      } catch (error) {
        console.error('Error removing like:', error)
        showToast(error.response?.data?.message || error.message || 'Error removing like', 'error')
      }
    }
    
    const removeDislike = async () => {
      try {
        const response = await axios.post(`/token/${tokenId.value}/undislike`, { userTag: userTag.value })
        
        if (response.data.success) {
          // Обновляем данные токена
          tokenData.value = response.data.tokenLaunch
          showToast('Dislike removed', 'info')
        } else {
          throw new Error(response.data.message || 'Failed to remove dislike')
        }
      } catch (error) {
        console.error('Error removing dislike:', error)
        showToast(error.response?.data?.message || error.message || 'Error removing dislike', 'error')
      }
    }

    const formatDate = (dateString) => {
      if (!dateString) return 'N/A';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', { 
        year: 'numeric', 
        month: 'short', 
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    }

    const formatLamports = (lamports) => {
      if (!lamports) return '0'
      return (lamports / 1e9).toFixed(2)
    }

    const formatUnlockVia = (unlockVia) => {
      const unlockMap = {
        '10m': '10 minutes',
        '30m': '30 minutes', 
        '1h': '1 hour',
        '2h': '2 hours',
        '1d': '1 day',
        '1w': '1 week'
      }
      return unlockMap[unlockVia] || unlockVia
    }

    const getSquadUnlockCountdown = () => {
      // Если есть unlockedAt, значит разблокировка уже произошла
      if (tokenData.value?.unlockedAt) {
        return `Unlocked ${formatDate(tokenData.value.unlockedAt)}`;
      }
      
      if (!tokenData.value?.unlockTime) return null;
      
      const unlockTime = new Date(tokenData.value.unlockTime);
      const now = new Date();
      const timeDiff = unlockTime.getTime() - now.getTime();
      
      if (timeDiff <= 0) {
        return 'Squad unlocked';
      }
      
      const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
      const hours = Math.floor((timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));
      
      if (days > 0) {
        return `${days}d ${hours}h ${minutes}m`;
      } else if (hours > 0) {
        return `${hours}h ${minutes}m`;
      } else {
        return `${minutes}m`;
      }
    }

    const isSquadLocked = () => {
      // Если есть unlockedAt, значит разблокировка уже произошла
      if (tokenData.value?.unlockedAt) return false;
      
      if (!tokenData.value?.unlockTime) return false;
      const unlockTime = new Date(tokenData.value.unlockTime);
      const now = new Date();
      return now < unlockTime;
    }

    const getSquadLockStatus = () => {
      if (tokenData.value?.unlockedAt) {
        return {
          isLocked: false,
          message: 'Squad tokens unlocked',
          date: formatDate(tokenData.value.unlockedAt),
          icon: 'unlock'
        };
      }
      
      if (!tokenData.value?.unlockTime) {
        return null;
      }
      
      const unlockTime = new Date(tokenData.value.unlockTime);
      const now = new Date();
      
      if (now >= unlockTime) {
        return {
          isLocked: false,
          message: 'Squad tokens unlocked',
          date: formatDate(unlockTime),
          icon: 'unlock'
        };
      }
      
      return {
        isLocked: true,
        message: 'Squad tokens locked',
        countdown: getSquadUnlockCountdown(),
        icon: 'lock'
      };
    }

    const getStatusText = (status) => {
      const statusMap = {
        active: 'Active',
        minting: 'Minting',
        launched: 'Launched',
        locked: 'Locked'
      }
      return statusMap[status] || 'Unknown'
    }

    const getUnlockLabel = () => {
      const now = new Date()
      if (tokenData.value?.unlockTime && new Date(tokenData.value.unlockTime) > now) {
        return 'Unlocks At'
      }
      return 'Unlock Time'
    }

    const formatTime = (date) => {
      return new Intl.DateTimeFormat('en-US', {
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      }).format(date)
    }

    const showToast = (message, type = 'info') => {
      toast.value.show = true;
      toast.value.message = message;
      toast.value.type = type;
      toast.value.duration = 3000;
    }

    const handleSquadJoined = (squadMember) => {
      // Можно добавить уведомление или другую логику после успешного присоединения к Squad
      console.log('Successfully joined squad:', squadMember);
      showToast('Successfully joined the squad!', 'success');
      // Перезагружаем данные токена после присоединения к squad
      loadTokenData(false)
    }

    const startAutoRefresh = () => {
      refreshInterval.value = setInterval(() => {
        loadTokenData(false) // Обновляем без показа загрузки
      }, 10000) // Каждые 10 секунд
    }

    const stopAutoRefresh = () => {
      if (refreshInterval.value) {
        clearInterval(refreshInterval.value)
        refreshInterval.value = null
      }
    }

    // Следим за изменением tokenId
    watch(tokenId, async (newTokenId) => {
      if (newTokenId) {
        stopAutoRefresh()
        await loadTokenData()
        startAutoRefresh()
      }
    })

    // Определяем размер экрана и автоматически сворачиваем NFT на мобильных
    const isMobile = () => window.innerWidth <= 768
    
    const handleResize = () => {
      if (isMobile()) {
        nftCollapsed.value = true
      } else {
        nftCollapsed.value = false
      }
    }
    
    onMounted(async () => {
      // Автоматически сворачиваем NFT на мобильных при загрузке
      if (isMobile()) {
        nftCollapsed.value = true
      }
      
      // Добавляем обработчик изменения размера окна
      window.addEventListener('resize', handleResize)
      
      await loadTokenData()
      startAutoRefresh()
    })

    onUnmounted(() => {
      stopAutoRefresh()
      window.removeEventListener('resize', handleResize)
    })

    const isMinSquadReached = () => {
      if (!tokenData.value) return false;
      return tokenData.value.currentSquadLamports >= tokenData.value.minSquadLamports;
    }

    const showLaunchCountdown = () => {
      if (!tokenData.value || tokenData.value.status !== 'active') return false;
      return isMinSquadReached() && tokenData.value.checkSquadCompletedAt && !tokenData.value.squadCompleted;
    }

    const getLaunchCountdown = () => {
      if (!tokenData.value?.checkSquadCompletedAt) return 'Calculating...';
      
      const checkTime = new Date(tokenData.value.checkSquadCompletedAt);
      const launchTime = new Date(checkTime.getTime() + 15 * 60 * 1000); // +15 минут
      const now = new Date();
      const timeDiff = launchTime.getTime() - now.getTime();
      
      if (timeDiff <= 0) {
        return 'Launching soon...';
      }
      
      const minutes = Math.floor(timeDiff / (1000 * 60));
      const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);
      
      return `${minutes}m ${seconds}s`;
    }

    const isEligibleForNft = () => {
      if (!tokenData.value || !userTag.value) return false
      // Проверяем, был ли пользователь в squad до запуска токена
      // Это временная логика для фронтенда - позже нужно будет получать данные с бэкенда
      return tokenData.value.status === 'launched' || tokenData.value.status === 'locked'
    }

    const mintNft = async () => {
      if (!isEligibleForNft()) return
      
      mintingNft.value = true
      try {
        // Здесь будет логика минтинга NFT через API
        await new Promise(resolve => setTimeout(resolve, 2000)) // Имитация запроса
        showToast('NFT minted successfully!', 'success')
      } catch (error) {
        console.error('Error minting NFT:', error)
        showToast('Failed to mint NFT', 'error')
      } finally {
        mintingNft.value = false
      }
    }

    const toggleTechnicalDetails = () => {
      showTechnicalDetails.value = !showTechnicalDetails.value
    }

    const getMainSectionTitle = () => {
      const status = tokenData.value?.status;
      const statusMap = {
        active: 'Collecting Squad',
        minting: 'Preparing Launch',
        launched: 'Launched',
        locked: 'Liquidity Locked'
      };
      return statusMap[status] || 'Unknown';
    };

    const shouldShowNftSection = () => {
      const status = tokenData.value?.status;
      return status === 'active' || status === 'minting' || status === 'launched' || status === 'locked';
    };

    const shareOnX = () => {
      if (!tokenData.value) return
      
      const tokenName = tokenData.value.name
      const tokenSymbol = tokenData.value.symbol
      const currentUrl = window.location.href
      const status = getStatusText(tokenData.value.status)
      
      let tweetText = `🚀 Check out ${tokenName} (${tokenSymbol}) - ${status}!`
      
      if (tokenData.value.maxSquadLamports > 0) {
        const progress = Math.round(tokenData.value.squadProgress || 0)
        const collected = formatLamports(tokenData.value.currentSquadLamports)
        const target = formatLamports(tokenData.value.maxSquadLamports)
        
        tweetText += `\n\n💰 Squad Progress: ${collected}/${target} SOL (${progress}%)`
      }
      
      tweetText += `\n\n#MemeToken #Solana #Crypto`
      
      const twitterUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(tweetText)}&url=${encodeURIComponent(currentUrl)}`
      
      window.open(twitterUrl, '_blank', 'width=600,height=400')
    }

    const copyMintAddress = async () => {
      if (!tokenData.value?.mint) return
      
      try {
        await navigator.clipboard.writeText(tokenData.value.mint)
        mintCopied.value = true
        showToast('Mint address copied to clipboard!', 'success')
        
        // Reset the copied state after 2 seconds
        setTimeout(() => {
          mintCopied.value = false
        }, 2000)
      } catch (error) {
        console.error('Failed to copy mint address:', error)
        showToast('Failed to copy mint address', 'error')
      }
    }

    const formatMintAddress = (mint) => {
      if (!mint) return 'N/A';
      const firstSix = mint.slice(0, 6);
      const lastSix = mint.slice(-6);
      return `${firstSix}...${lastSix}`;
    }

    return {
      loading,
      error,
      tokenData,
      userTag,
      toast,
      tokenId,
      wallet,
      hasUserLiked,
      hasUserDisliked,
      lastUpdated,
      loadTokenData,
      handleLike,
      handleDislike,
      removeLike,
      removeDislike,
      formatDate,
      showToast,
      handleSquadJoined,
      formatLamports,
      formatUnlockVia,
      getSquadUnlockCountdown,
      isSquadLocked,
      getSquadLockStatus,
      getStatusText,
      getUnlockLabel,
      formatTime,
      isMinSquadReached,
      showLaunchCountdown,
      getLaunchCountdown,
      isEligibleForNft,
      mintNft,
      toggleTechnicalDetails,
      showTechnicalDetails,
      mintingNft,
      getMainSectionTitle,
      shouldShowNftSection,
      shareOnX,
      showImagePopup,
      nftCollapsed,
      mintCopied,
      copyMintAddress,
      formatMintAddress
    }
  }
}
</script>

