}

.token-description h3, .token-links h3 {
  margin: 0 0 1rem;
  font-size: 1.2rem;
  color: #1e293b;
}

.token-description p {
  margin: 0 0 1.5rem;
  color: #64748b;
  line-height: 1.6;
}

.links-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.token-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background-color: #f8fafc;
  color: #3b82f6;
  border-radius: 4px;
  text-decoration: none;
