}

.token-link:hover {
  background-color: #eff6ff;
}

.no-links {
  color: #94a3b8;
  font-style: italic;
}

.token-links {
  display: flex;
  margin-top: 0.75rem;
  gap: 1rem;
  flex-wrap: wrap;
  width: 100%;
}

.token-link {
  display: inline-flex;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: #fff3e6;
  color: #ff6b00;
  border-radius: 6px;
  text-decoration: none;
