}

.info-section {
  margin-bottom: 2rem;
  background: white;
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  border: 1px solid rgba(255, 107, 0, 0.1);
}

.main-info {
  margin-bottom: 2rem;
}

.status-content {
  margin-top: 1rem;
}

.creator-link {
  background: linear-gradient(135deg, #ff6b00, #ff9d4d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  text-decoration: none;
}

.creator-link:hover {
  text-decoration: underline;
}

.squad-progress-section {
  margin-top: 1.5rem;
}

.squad-progress-card h4 {
  margin: 0;
  font-size: 1rem;
  color: #1e293b;
  display: flex;
  align-items: center;
  gap: 8px;
}

.minting-message {
  margin-top: 1rem;
  padding: 1rem;
  background: linear-gradient(135deg, #fef3c7, #fed7aa);
  border-radius: 8px;
  display: flex;
  align-items: center;
  gap: 12px;
  color: #92400e;
}

.minting-message i {
  font-size: 1.2rem;
  animation: pulse 1.5s infinite;
}

.minting-message p {
  margin: 0;
  font-weight: 500;
}

.token-address {
  font-family: 'Courier New', monospace;
  font-size: 0.8rem;
  background: #f8fafc;
  padding: 4px 8px;
  border-radius: 4px;
  word-break: break-all;
}

/* Premium NFT Section Styles */
.nft-premium-section {
  margin-top: 2rem;
}

.nft-premium-card {
  position: relative;
  background: linear-gradient(135deg, #ff9800 0%, #ff5722 50%, #e65100 100%);
  border-radius: 20px;
  padding: 0;
  transition: all 0.4s ease;
}

.nft-mobile-header {
  display: none; /* Hidden on desktop */
  padding: 0.75rem 1rem;
  cursor: pointer;
  border-radius: 20px 20px 0 0;
  user-select: none;
}

.nft-mobile-title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: white;
  font-weight: 600;
  font-size: 1rem;
}

.nft-mobile-title i {
  color: #ffd700;
  font-size: 1rem;
}

.nft-toggle-icon {
  color: white;
  transition: transform 0.3s ease;
  font-size: 0.875rem;
}

.nft-toggle-icon.rotated {
  transform: rotate(180deg);
}

.nft-content {
  position: relative;
  z-index: 2;
  padding: 1.25rem;
  color: white;
  transition: all 0.3s ease;
  overflow: visible;
}

/* Collapsing only works on mobile */
@media (max-width: 768px) {
  .nft-content {
    overflow: hidden;
  }
  
  .nft-content.nft-collapsed {
    max-height: 0;
    padding: 0 1.25rem;
    opacity: 0;
  }
}

.nft-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 1rem;
  position: relative;
}

.nft-premium-icon {
  width: 35px;
  height: 35px;
  background: linear-gradient(135deg, #ffd700, #ffb300, #ff8f00);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  color: #1a1a1a;
  box-shadow: 
    0 4px 10px rgba(255, 215, 0, 0.5),
    0 2px 4px rgba(255, 179, 0, 0.3),
    inset 0 1px 2px rgba(255, 255, 255, 0.3);
  flex-shrink: 0;
  position: relative;
  animation: crown-pulse 3s ease-in-out infinite;
}

@keyframes crown-pulse {
  0%, 100% { 
    transform: scale(1);
    box-shadow: 
      0 4px 10px rgba(255, 215, 0, 0.5),
      0 2px 4px rgba(255, 179, 0, 0.3),
      inset 0 1px 2px rgba(255, 255, 255, 0.3);
  }
  50% { 
    transform: scale(1.05);
    box-shadow: 
      0 6px 15px rgba(255, 215, 0, 0.6),
      0 3px 6px rgba(255, 179, 0, 0.4),
      inset 0 1px 2px rgba(255, 255, 255, 0.4);
  }
}

.nft-premium-icon::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background: linear-gradient(45deg, #ffd700, transparent, #ff8f00, transparent, #ffd700);
  border-radius: 50%;
  z-index: -1;
  animation: rotate-border 3s linear infinite;
}

@keyframes rotate-border {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.nft-title-section {
  flex: 1;
}

.nft-title-section h3 {
  margin: 0 0 0.25rem 0;
  font-size: 1.25rem;
  font-weight: 800;
  color: white;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  position: relative;
}

.nft-title-section h3::after {
  content: '';
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 30px;
  height: 2px;
  background: linear-gradient(90deg, #ffd700, #ff8f00);
  border-radius: 1px;
}

.nft-title-section p {
  margin: 0;
  opacity: 0.95;
  font-size: 0.95rem;
  font-weight: 500;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.nft-rarity-badge {
  background: linear-gradient(135deg, rgba(255, 215, 0, 0.9), rgba(255, 143, 0, 0.8));
  backdrop-filter: blur(15px);
  border-radius: 15px;
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
  font-weight: 800;
  color: #1a1a1a;
  border: 2px solid rgba(255, 215, 0, 0.6);
  flex-shrink: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 12px rgba(255, 215, 0, 0.3);
  animation: badge-glow 2s ease-in-out infinite alternate;
}

@keyframes badge-glow {
  0% { box-shadow: 0 4px 12px rgba(255, 215, 0, 0.3); }
  100% { box-shadow: 0 6px 18px rgba(255, 215, 0, 0.5); }
}

.nft-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 0.5rem;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0.05));
  border-radius: 8px;
  font-size: 0.875rem;
  font-weight: 600;
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.feature-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s ease;
}

.feature-item:hover::before {
  left: 100%;
}

.feature-item:hover {
  transform: translateY(-2px);
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
  box-shadow: 0 8px 20px rgba(255, 152, 0, 0.2);
}

.feature-item i {
  color: #ffd700;
  font-size: 1rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

/* Enhanced Action Area */
.nft-action-area {
  text-align: center;
  position: relative;
  margin-top: 1rem;
}

.nft-eligible .eligibility-message {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
  padding: 0.6rem 0.5rem 0;
  font-weight: 600;
  font-size: 0.9rem;
}

.nft-eligible .eligibility-message i {
  color: #fff;
  font-size: 1rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
  animation: success-pulse 2s ease-in-out infinite;
}

@keyframes success-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.mint-nft-btn {
  background: #fff;
  color: #ff6b00;
  border: none;
  border-radius: 10px;
  padding: 0.8rem 1.6rem;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.4s ease;
  min-width: 180px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  overflow: hidden;
}

.mint-nft-btn:hover {
  transform: translateY(-3px) scale(1.02);
}

.mint-nft-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

.update-indicator {
  text-align: center;
  color: #666;
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 16px;
  margin-top: 16px;
