}

.back-button {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background-color: #ff6b00;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  font-weight: 600;
  transition: background-color 0.2s;
}

.back-button:hover {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.token-content {
  padding-bottom: 3rem;
}

