/* Squad Progress Elements */
.squad-progress-card {
  background: linear-gradient(135deg, #f8fafc, #e2e8f0);
  border-radius: 12px;
  padding: 1.5rem;
  border: 2px solid rgba(255, 107, 0, 0.1);
