<template>
  <div class="token-squad">
    <div v-if="loading" class="loading">
      <div class="spinner"></div>
      <p>Loading squad information...</p>
    </div>
    
    <div v-else-if="error" class="error">
      <p>{{ error }}</p>
    </div>
    
    <div v-else class="squad-container">
      <div class="squad-header">
        <h2>Squad Members</h2>
        <div class="squad-stats">
          <div class="stat-item">
            <span class="stat-value">{{ squadData.totalMembers || 0 }}</span>
            <span class="stat-label">Members</span>
          </div>
          <div class="stat-item">
            <span class="stat-value">{{ formatSol(tokenLaunch.currentSquadLamports / LAMPORTS_PER_SOL || 0) }}</span>
            <span class="stat-label">Total SOL</span>
          </div>
        </div>
      </div>
      
      <div v-if="!isUserInSquad && isWalletConnected" class="join-squad">
        <!-- Squad collection completed notice -->
        <div v-if="isSquadCollectionCompleted" class="squad-completed-notice">
          <div class="completed-icon">
            <i class="fas fa-check-circle"></i>
          </div>
          <div class="completed-content">
            <h3>Squad Collection Completed</h3>
            <p v-if="tokenLaunch.status === 'minting'">
              The squad funding has been completed and the token is being prepared for launch.
              New investments are no longer accepted.
            </p>
            <p v-else-if="tokenLaunch.status === 'launched'">
              The token has been successfully launched! Squad collection is now closed.
            </p>
            <p v-else-if="tokenLaunch.status === 'locked'">
              The token has been launched and liquidity is locked. Squad collection is now closed.
            </p>
            <div v-if="tokenLaunch.squadCompletedAt" class="completion-date">
              <i class="fas fa-calendar"></i>
              <span>Completed on {{ formatDate(tokenLaunch.squadCompletedAt) }}</span>
            </div>

            <!-- link to the tokens page -->
            <router-link to="/tokens" class="squad-back-button">
              <i class="fas fa-arrow-left"></i> Back to tokens
            </router-link>
          </div>
        </div>
        
        <!-- Join form (only shown when squad collection is active) -->
        <div v-else>
          <h3>Join the Squad</h3>
          <div class="join-form">
            <div class="form-group">
              <label for="solAmount">SOL Amount</label>
              <!-- SOL Amount buttons -->
              <div class="amount-buttons">
                <button 
                  v-for="amount in [0.01, 0.05, 0.1, 0.2, 0.25]" 
                  :key="amount"
                  type="button" 
                  class="amount-button" 
                  :class="{ 'active': solAmount === amount }"
                  @click="solAmount = amount"
                  :disabled="joiningSquad"
                >
                  {{ amount }} SOL
                </button>
              </div>
              <div class="input-with-icon">
                <input 
                  type="number" 
                  id="solAmount" 
                  v-model="solAmount" 
                  min="0.01" 
                  max="0.25"
                  step="0.01" 
                  class="form-control"
                  :disabled="joiningSquad"
                />
                <span class="input-icon">SOL</span>
              </div>
              <small class="form-text">Minimum amount: 0.01 SOL | Maximum amount: 0.25 SOL</small> 
            </div>
            
            <button 
              @click="joinSquad" 
              class="join-button" 
              :disabled="!canJoin || joiningSquad"
            >
              <span v-if="joiningSquad">
                <div class="button-spinner"></div> Processing...
              </span>
              <span v-else>Join Squad</span>
            </button>
            <!-- commission section -->
            <div class="commission-info">
              <p>You will pay {{ calculateTotalAmount }} SOL including fees (referral system 5%, our commission 5%, Associated Token Account creation 0.002 SOL you can close your ATA accaunt after launch <router-link to="/cashback">here</router-link>)</p>
            </div>
          </div>
        </div>
      </div>
      
      <div v-else-if="!isWalletConnected" class="wallet-notice">
        <p>Connect your wallet to join the squad</p>
        <router-link to="/my">Connect Wallet</router-link>
      </div>
      
      <div v-else-if="isUserInSquad" class="already-joined">
        <div class="success-icon">✓</div>
        <p>You've already joined this squad!</p>
        <div class="user-contribution">
          <span>Your contribution: </span>
          <strong>{{ formatSol(userContribution) }} SOL</strong>
        </div>
        <button 
          class="leave-squad-button" 
          :disabled="leavingSquad" 
          @click="showLeaveSquadPopup = true"
        >
          <span v-if="leavingSquad" class="button-spinner"></span>
          {{ leavingSquad ? 'Leaving...' : 'Leave Squad' }}
        </button>
      </div>
      
      <div v-else-if="hasUserLeftSquad" class="left-squad">
        <div class="warning-icon">⚠️</div>
        <p>You have left this squad</p>
        <p class="left-squad-message">Your funds have been returned. You can join again if you wish.</p>
        <div v-if="!isSquadCollectionCompleted">
          <div class="form-group">
            <label for="solAmount">Amount (SOL):</label>
            <div class="input-with-icon">
              <input 
                type="number" 
                id="solAmount" 
                v-model="solAmount" 
                step="0.001" 
                min="0.01" 
                class="form-control"
                placeholder="0.01"
              />
              <span class="input-icon">SOL</span>
            </div>
            <small class="form-text">Total: {{ calculateTotalAmount }} SOL (includes fees)</small>
          </div>
          <button 
            class="join-squad-button" 
            :disabled="!canJoin || joiningSquad" 
            @click="joinSquad"
          >
            <span v-if="joiningSquad" class="button-spinner"></span>
            {{ joiningSquad ? 'Joining...' : 'Join Squad Again' }}
          </button>
        </div>
      </div>
      
      <div class="members-table">
        <h3>{{ squadData.members && squadData.members.length ? 'Members' : 'No members yet' }}</h3>
        
        <div v-if="squadData.members && squadData.members.length > 0" class="squad-cards">
          <div v-for="member in squadData.members" :key="member.transaction" class="squad-card">
            <div class="card-header">
              <router-link :to="`/player/${member.userTag}`" class="card-user">{{ member.userTag }}</router-link>
              <span :class="['status-badge', member.confirmed ? 'confirmed' : 'left']">
                {{ member.confirmed ? 'Confirmed' : 'Left' }}
              </span>
            </div>
            
            <div class="card-body">
              <div class="card-row">
                <span class="card-label">Wallet:</span>
                <span class="card-value wallet">{{ formatAddress(member.wallet) }}</span>
              </div>
              
              <div class="card-row">
                <span class="card-label">Amount:</span>
                <span class="card-value amount">{{ formatSol(member.lamports / LAMPORTS_PER_SOL) }} SOL</span>
              </div>
              
              <div class="card-row">
                <span class="card-label">Date:</span>
                <span class="card-value">{{ formatDate(member.createdAt) }}</span>
              </div>
              
              <div class="card-row" v-if="member.transaction">
                <span class="card-label">TX:</span>
                <a 
                  :href="`https://solscan.io/tx/${member.transaction}`" 
                  target="_blank" 
                  class="card-value tx-link"
                >
                  {{ formatAddress(member.transaction) }}
                  <i class="fas fa-external-link-alt"></i>
                </a>
              </div>

              <div class="card-row" v-if="member.refundTx">
                <span class="card-label">Refund TX:</span>
                <a 
                  :href="`https://solscan.io/tx/${member.refundTx}`" 
                  target="_blank" 
                  class="card-value tx-link"
                >
                  {{ formatAddress(member.refundTx) }}
                  <i class="fas fa-external-link-alt"></i>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- Popup для подтверждения выхода из Squad -->
  <div class="modal" v-if="showLeaveSquadPopup">
    <div class="modal-content">
      <div class="modal-header">
        <h3>Leave Squad Confirmation</h3>
        <button class="close-button" @click="showLeaveSquadPopup = false">×</button>
      </div>
      <div class="modal-body">
        <p><strong>Warning:</strong> If you leave the squad:</p>
        <ul>
          <li>Your funds will be returned to your wallet (minus network fees)</li>
          <li>The transaction fee is non-refundable</li>
          <li>You will <strong>not</strong> receive any tokens from this project after launch</li>
        </ul>
        <p>Are you sure you want to leave this squad?</p>
      </div>
      <div class="modal-footer">
        <button class="cancel-button" @click="showLeaveSquadPopup = false">Cancel</button>
        <button 
          class="confirm-button" 
          :disabled="leavingSquad" 
          @click="leaveSquad"
        >
          <span v-if="leavingSquad" class="button-spinner"></span>
          {{ leavingSquad ? 'Leaving...' : 'Leave Squad' }}
        </button>
      </div>
    </div>
  </div>

  <!-- Popup для отображения результата выхода из Squad -->
  <div class="modal" v-if="showResultPopup">
    <div class="modal-content">
      <div class="modal-header">
        <h3>{{ resultPopup.title }}</h3>
        <button class="close-button" @click="showResultPopup = false">×</button>
      </div>
      <div class="modal-body" :class="resultPopup.type">
        <p>{{ resultPopup.message }}</p>
        <div v-if="resultPopup.txSignature" class="transaction-info">
          <p>Transaction:</p>
          <a :href="`https://solscan.io/tx/${resultPopup.txSignature}`" target="_blank" class="tx-link">
            {{ formatAddress(resultPopup.txSignature) }}
            <i class="fas fa-external-link-alt"></i>
          </a>
        </div>
      </div>
      <div class="modal-footer">
        <button class="confirm-button" @click="showResultPopup = false">OK</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue';
import { useRoute } from 'vue-router';
import { useWallet } from 'solana-wallets-vue';
import { SystemProgram } from '@solana/web3.js';
import { Connection, PublicKey, LAMPORTS_PER_SOL, Transaction } from '@solana/web3.js';
import axios from 'axios';
import store from '@/vuex';

// Props
const props = defineProps({
  tokenId: {
    type: String,
    required: true
  },
  userTag: {
    type: String,
    default: ''
  }
});

// Emits
const emit = defineEmits(['joined']);

// State
const squadData = ref({ members: [] });
const tokenLaunch = ref({});
const loading = ref(true);
const error = ref('');
const solAmount = ref(0.01);
const joiningSquad = ref(false);
const leavingSquad = ref(false);

// Расчет общей суммы с учетом комиссий
const calculateTotalAmount = computed(() => {
  // Базовая сумма
  const baseAmount = solAmount.value;
  // Комиссия сайта и реферальной системы (10%)
  const feeAmount = baseAmount * 0.1;
  // Стоимость создания Associated Token Account
  const ataCreationCost = 0.002;
  // Общая сумма
  const totalAmount = baseAmount + feeAmount + ataCreationCost;
  // Форматируем до 3 знаков после запятой
  return totalAmount.toFixed(3);
});

// Popup состояния
const showLeaveSquadPopup = ref(false);
const showResultPopup = ref(false);
const resultPopup = ref({
  title: '',
  message: '',
  type: 'success',
  txSignature: ''
});
const userContribution = ref(0);
const wallet = computed(() => store.getters.walletAddress);
const isWalletConnected = computed(() => !!wallet.value);

// Проверяем, является ли пользователь участником Squad
const isUserInSquad = computed(() => {
  if (!squadData.value.members || !props.userTag) return false;
  const userMember = squadData.value.members.find(member => member.userTag === props.userTag);
  // Пользователь считается в squad если у него есть запись И (confirmed=true ИЛИ lamports > 0)
  return userMember && (userMember.confirmed || userMember.lamports > 0);
});

// Проверяем, вышел ли пользователь из Squad (lamports = 0 и !confirmed)
const hasUserLeftSquad = computed(() => {
  if (!squadData.value.members || !props.userTag) return false;
  const userMember = squadData.value.members.find(member => member.userTag === props.userTag);
  return userMember && userMember.lamports === 0 && !userMember.confirmed;
});

// Проверяем, завершен ли сбор squad (заблокирован ли для новых инвестиций)
const isSquadCollectionCompleted = computed(() => {
  console.log('tokenLaunch.value', tokenLaunch.value);
  const status = tokenLaunch.value?.status;
  console.log('status', status);
  console.log('Available statuses:', ['minting', 'launched', 'locked']);
  return status === 'minting' || status === 'launched' || status === 'locked';
});

// Обновляем userContribution при изменении squadData
watch(() => squadData.value.members, (newMembers) => {
  if (newMembers && props.userTag) {
    const userMember = newMembers.find(member => member.userTag === props.userTag);
    if (userMember) {
      userContribution.value = userMember.lamports / LAMPORTS_PER_SOL;
    }
  }
}, { immediate: true });

const canJoin = computed(() => {
  return isWalletConnected.value && 
         solAmount.value >= 0.01 && 
         (!isUserInSquad.value || hasUserLeftSquad.value) &&
         !isSquadCollectionCompleted.value;
});

// Загружаем данные при монтировании компонента
onMounted(async () => {
  await loadSquadData();
});

// Methods
const loadSquadData = async () => {
  loading.value = true;
  error.value = '';
  
  try {
    const response = await axios.get(`/token/${props.tokenId}/squad`);
    if (response.data.success) {
      tokenLaunch.value = response.data.tokenLaunch;
      squadData.value = response.data.squad;
    } else {
      error.value = response.data.message || 'Failed to load squad data';
    }
  } catch (err) {
    console.error('Error loading squad data:', err);
    error.value = err.response?.data?.message || 'Error loading squad data';
  } finally {
    loading.value = false;
  }
};

const formatAddress = (address) => {
  if (!address) return '';
  return `${address.substring(0, 4)}...${address.substring(address.length - 4)}`;
};

const formatDate = (dateString) => {
  if (!dateString) return '';
  return new Date(dateString).toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
};

const showPopupResult = (title, message, type = 'success', txSignature = '') => {
  resultPopup.value = {
    title,
    message,
    type,
    txSignature
  };
  showResultPopup.value = true;
};

const formatSol = (amount) => {
  return parseFloat(amount).toFixed(3);
};

const joinSquad = async () => {
  if (!canJoin.value || joiningSquad.value) return;
  
  joiningSquad.value = true;
  
  try {
    // Check wallet connection
    if (!wallet.value) {
      throw new Error('Wallet not connected');
    }
    
    // Get transaction data from server
    const transactionResponse = await axios.post(`/token/${props.tokenId}/squad/transaction`, {
      lamports: solAmount.value * LAMPORTS_PER_SOL,
      wallet: wallet.value,
      tokenId: props.tokenId,
    });
    
    if (transactionResponse.data.success) {
      // Get serialized transaction and other data from server
      const { 
        serializedTransaction, 
        lamports,
        feeAmount,
        totalLamports 
      } = transactionResponse.data;
      
      console.log('Transaction data from server:', {
        lamports,
        feeAmount,
        totalLamports
      });
      
      // Create connection to Solana network
      const connection = new Connection(process.env.VUE_APP_SOLANA_RPC_URL || 'https://sleek-icy-frog.solana-mainnet.quiknode.pro/9d4845e2d4c60caf45dc4f10d0bdcfdbcd6a321c/');
      
      // Get latest blockhash directly from the network
      const { blockhash, lastValidBlockHeight } = await connection.getLatestBlockhash('finalized');
      console.log('Got blockhash from client:', { blockhash, lastValidBlockHeight });
      
      // Deserialize transaction from server
      const transaction = Transaction.from(Buffer.from(serializedTransaction, 'base64'));
      
      // Update transaction with fresh blockhash
      transaction.recentBlockhash = blockhash;
      transaction.lastValidBlockHeight = lastValidBlockHeight;

      
      // // Добавьте инструкции для увеличения приоритета
      // import { ComputeBudgetProgram } from '@solana/web3.js';

      // const PRIORITY_MICROLAMPORTS = 25_000;  // 0.000025 SOL за 1 CU
      // const CU_LIMIT = 200_000;               // Лимит вычислительных единиц

      // // Добавьте инструкции в начало транзакции
      // transaction.instructions.unshift(
      //   ComputeBudgetProgram.setComputeUnitLimit({ units: CU_LIMIT }),
      //   ComputeBudgetProgram.setComputeUnitPrice({
      //     microLamports: PRIORITY_MICROLAMPORTS,
      //   })
      // );
      
      // Check if Phantom is available
      if (!window.solana) {
        throw new Error('Phantom wallet not found');
      }
      
      // Sign transaction using Phantom
      const signedTransaction = await window.solana.signTransaction(transaction);
      
      // Send signed transaction
      const signature = await connection.sendRawTransaction(signedTransaction.serialize());
      
      console.log('Transaction sent with signature:', signature);
      
      // Wait for confirmation
      const confirmation = await connection.confirmTransaction({
        signature,
        blockhash,
        lastValidBlockHeight
      }, 'confirmed');
      
      console.log('Transaction confirmation:', confirmation);
      
      // Verify transaction on server
      const confirmResponse = await axios.post(`/token/${props.tokenId}/squad/verify`, {
        transaction: signature,
        lamports: solAmount.value * LAMPORTS_PER_SOL,
        wallet: wallet.value
      });
      
      if (confirmResponse.data.success) {
        showPopupResult('Success', 'Successfully joined the squad!', 'success', signature);
        loadSquadData();
        emit('joined');
      } else {
        showPopupResult('Failed to Join', `Failed to join squad: ${confirmResponse.data.message}`, 'error');
      }
    } else {
      showPopupResult('Transaction Error', `Failed to create transaction: ${transactionResponse.data.message}`, 'error');
    }
  } catch (error) {
    console.error('Error joining squad:', error);
    showPopupResult('Error', `Error joining squad: ${error.message}`, 'error');
  } finally {
    joiningSquad.value = false;
  }
};

const leaveSquad = async () => {
  try {
    if (!isWalletConnected.value || !isUserInSquad.value) {
      showPopupResult('Error', 'You are not a member of this squad', 'error');
      return;
    }
    
    leavingSquad.value = true;
    showLeaveSquadPopup.value = false;
    
    // Вызываем API для выхода из Squad
    const response = await axios.post(`/token/${props.tokenId}/squad/leave`);
    
    if (response.data.success) {
      const { txSignature } = response.data;
      showPopupResult(
        'Success', 
        'You have successfully left the squad. Your funds (minus network fees) have been returned to your wallet.', 
        'success',
        txSignature
      );
      // Обновляем данные
      loadSquadData();
    } else {
      showPopupResult('Failed to Leave', `Failed to leave squad: ${response.data.message}`, 'error');
    }
  } catch (error) {
    console.error('Error leaving squad:', error);
    showPopupResult('Error', `Error leaving squad: ${error.response.data.message}`, 'error');
  } finally {
    leavingSquad.value = false;
  }
};
</script>

